* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

:root {
  font-size: 62.5%;
  --orange: #f26002;
  --grey: #F2F2F2;
  --greyish-brown: #4a4a4a;
  --white: #ffffff;
  --font-p: 'Roboto', sans-serif;
  --font-h: 'Roboto Slab', serif; }

.header {
  position: fixed;
  width: 37px;
  height: 34px;
  top: 0;
  z-index: 10;
  background-color: #ff6f00; }

.header-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 11px; }

.menu-img {
  width: 13px;
  height: 11px; }

/* This is the button to go up */
.go-up {
  position: relative;
  bottom: 3rem;
  left: 45%;
  display: flex;
  justify-content: flex-start;
  text-align: center;
  z-index: 10; }

.up {
  background-color: var(--white);
  width: 45px;
  height: 45px;
  margin: 5px;
  padding: 10px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute; }

/* This  is an animation with :hover */
.up:hover {
  transform: scale(1.5);
  background-color: #04ae65;
  transition: all .3s ease-in-out; }

@media all and (min-width: 768px) {
  .go-up {
    margin-left: 30px; } }

/* This is the footer, has been used flex */
.footer {
  height: 465px;
  background-color: var(--greyish-brown);
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  flex-direction: column;
  padding-left: 109px;
  padding-right: 109px;
  padding-top: 65px;
  padding-bottom: 36px;
  font-family: var(--font-p); }
  .footer .footer-menu-one {
    color: var(--white);
    list-style: none;
    font-size: 1.6rem;
    text-align: center;
    height: 120px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    text-decoration: none; }
    .footer .footer-menu-one .menu-one {
      color: var(--white);
      text-decoration: none;
      text-transform: uppercase; }
  .footer .footer-menu-two {
    color: var(--white);
    list-style: none;
    font-size: 1.6rem;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: 60px; }
    .footer .footer-menu-two .menu-two {
      color: var(--white);
      text-decoration: none;
      text-transform: uppercase; }
  .footer .footer-end {
    color: var(--white);
    list-style: none;
    font-size: 1.4rem;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    text-align: center; }

@media all and (min-width: 768px) {
  .footer {
    display: flex;
    flex-direction: row-reverse; } }

.hero {
  background-image: url("../images/cover.jpeg");
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 528.8px;
  background-size: contain;
  color: white;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative; }

.hero-title {
  font-size: 2.5rem;
  text-align: center;
  font-family: var(--font-h);
  width: 300px;
  text-transform: uppercase; }

.hero-title-two {
  padding: 34px;
  font-size: 1rem;
  text-align: center;
  font-family: var(--font-p); }

/* This is the button to  go down */
.down {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 37px;
  margin-top: 78px; }

.down-image {
  background-color: var(--white);
  width: 45px;
  height: 45px;
  margin: 5px;
  padding: 10px;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  border-radius: 50px;
  font-size: 11px;
  line-height: 32px;
  text-transform: uppercase;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center; }

@media all and (min-width: 768px) {
  .hero-title {
    font-size: 7rem;
    width: 900px; }
  .hero-title-two {
    font-size: 2.4rem; } }

@media all and (min-width: 1200px) {
  .hero {
    height: 804px; }
  .hero-title {
    font-size: 9rem;
    width: 1200px; }
  .hero-title-two {
    font-size: 2.4rem; } }

/* This the section one of the menu has been used flex */
.box-one {
  height: 376px;
  margin-top: 34px;
  margin-left: 20px;
  padding: 34px 12px 2px 13px;
  background-color: var(--white);
  align-items: flex-start; }
  .box-one .small {
    color: var(--orange);
    font-size: 1.4rem;
    font-family: var(--font-p); }
  .box-one .article .article-title {
    font-size: 2.4rem;
    font-weight: bold;
    padding-top: 13px;
    font-family: var(--font-h); }
  .box-one .article .article-p {
    font-size: 1.8rem;
    padding-top: 13px;
    padding-bottom: 24px;
    font-family: var(--font-p); }
  .box-one .article .box-one-button {
    border-radius: 22px;
    text-align: center;
    height: 4.5rem;
    width: 16rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: solid 2px var(--orange);
    margin-top: 30px;
    /*This an animation to disappear the word "Compar" with :hover*/ }
    .box-one .article .box-one-button .button {
      color: var(--orange);
      padding-left: 10px;
      padding-right: 10px;
      text-decoration: none;
      text-align: center;
      font-size: 2rem;
      font-family: var(--font-p); }
    .box-one .article .box-one-button a:hover {
      -webkit-text-fill-color: transparent;
      transition: all 0.3s ease-in-out; }

@media all and (min-width: 768px) {
  .box-one {
    height: 330px; }
    .box-one .article-p {
      height: 39px; } }

@media all and (min-width: 1200px) {
  .box-one {
    height: 360px; } }

/* This is the aside of the menu, has been used grid */
.container {
  display: grid;
  grid-template-rows: 1fr 3fr 1fr;
  padding-bottom: 49px;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 624px;
  background-color: var(--grey);
  padding-left: 48px;
  padding-right: 48px; }
  .container .container-title {
    padding-top: 29px;
    width: 200px;
    padding: 2px; }
    .container .container-title .title {
      margin-left: 40px;
      margin-right: -30px;
      font-size: 2rem;
      font-weight: bold;
      font-family: var(--font-h); }
  .container .container-p {
    grid-column: span 1;
    grid-row: span 3;
    font-size: 1.8rem;
    justify-content: space-between; }
    .container .container-p .p {
      padding: 15px; }
    .container .container-p .p-two {
      flex-direction: column;
      padding: 20px;
      font-family: var(--font-p);
      width: 250px; }
    .container .container-p .title-two {
      margin-bottom: 0px;
      font-size: 1.8rem;
      font-family: var(--font-h); }
  .container .container-button {
    border-radius: 22px;
    text-align: center;
    height: 4.5rem;
    width: 18rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--orange);
    margin-left: 45px;
    /* :hover */ }
    .container .container-button .button {
      color: var(--white);
      padding-left: 10px;
      padding-right: 10px;
      text-decoration: none;
      text-align: center;
      font-size: 2rem;
      font-family: var(--font-p); }
    .container .container-button a:hover {
      background-color: #b608ea; }

@media all and (min-width: 768px) {
  .container {
    display: grid;
    grid-template-columns: span 1;
    grid-template-rows: span 3;
    height: 410px;
    align-items: center;
    justify-content: center;
    text-align: center; }
    .container .container-title {
      display: flex;
      justify-content: center;
      text-align: center;
      font-size: 2.8rem;
      margin-left: 170px;
      width: 500px; }
    .container .container-p {
      display: grid;
      grid-column: span 3;
      grid-row: span 1;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      font-size: 2rem;
      padding: 30px;
      gap: 40px; }
    .container .container-button {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 400px; } }

@media all and (min-width: 1200px) {
  .container {
    height: 427px; }
    .container .container-title {
      font-size: 3.2rem;
      margin-left: 180px;
      margin-right: 50px; }
    .container .container-p .title-two {
      font-size: 2.4rem; }
    .container .container-p .p-two {
      font-size: 20px; }
    .container .container-button {
      justify-content: center;
      align-items: center;
      margin-left: 380px; } }
